import { Component, OnInit, NgZone, ViewChild } from '@angular/core';
import { ApiservicesService } from '../../provider/apiservices.service';
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';
import { MenuController } from '@ionic/angular';
import { DataTransferService } from '../../services/data-transfer.service';
import { LocalStorageService } from '../../services/local-storage.service';
import {IonContent} from '@ionic/angular';

@Component({
  selector: 'app-community-detail',
  templateUrl: './community-detail.page.html',
  styleUrls: ['./community-detail.page.scss'],
})

export class CommunityDetailPage implements OnInit{

  @ViewChild(IonContent, { read: IonContent }) myContent: IonContent;
  @ViewChild('theTarget') targetx: any;
  listPageData:any;
  brandDetailApiResult:any;
  brandDetailData:any;
  errorMessage:string="";
  myInfiniteScroll:any;
  myinfiniteScrollCall:boolean=false;
  value:number=0;
  data:any;
  showScroll:any;
  pdfData:any;

  constructor(private apiservices:ApiservicesService,private zone:NgZone,public menuCtrl:MenuController,
    private sendData:DataTransferService,private storageService:LocalStorageService) {

    this.listPageData=this.sendData.myParam;
    console.log("data from communityList==>",this.listPageData);
    if(this.listPageData!=undefined){
      this.showScroll=this.sendData.autoScroll;
      console.log("autoScrollClick show component click detail==>",this.showScroll);
    }

  }









  videoZoome(videoPath,videoType,index){  // this function is use for All Type of Media zoom
    this.zone.run(()=>{
      this.apiservices.videoZoome(videoPath,videoType,index);
    });
  }

  openSeperateBrowser(browserUrl){
    this.zone.run(()=>{
      this.apiservices.iab.create(browserUrl, "_system", this.apiservices.options);
    });
  }

  ngOnInit(){
    this.storageService.getStorage().then(res=>{
      this.brandDetailFunc();
      console.log("ionic lifecycle called getStorage==>",res);
    });
  }

  ionViewWillLeave(){
    this.apiservices.showCommentBox=false;
    this.apiservices.showReplyComment=false;
    console.log('brandDetailData==> ', this.brandDetailData);
    this.listPageData.like_status= this.brandDetailData.like_status;
    this.listPageData.total_comments= this.brandDetailData.total_comments;
    this.listPageData.total_likes= this.brandDetailData.total_likes;
    this.listPageData.comment_setting= this.brandDetailData.comment_setting;
    this.listPageData.like_setting= this.brandDetailData.like_setting;
  }

  brandDetailFunc(){
    this.zone.run(()=>{
      let apiKey={
        "module_id":this.listPageData.module_id,
        "post_id":this.listPageData.post_id
      };
      console.log("brand pending api keys==", apiKey);
      this.apiservices.apiCallWithLoginToken(URLS.communicationDetail, apiKey).subscribe((result) => {
        console.log("brand pending Api Result==", result);
        this.brandDetailApiResult=result;
        if (result.status == 1) {
          this.pdfData=result.data.attachment;
          this.brandDetailData = result.data;
          this.errorMessage='';
          this.sendData.alldata=this.brandDetailData;
          if(this.brandDetailData.comment_setting=="1"){
            this.apiservices.showCommentBox=true; 
            console.log("brand detail result==", this.apiservices.showCommentBox);
          }

          setTimeout(()=>{
            // this.updateScroll();
            if(this.showScroll=='true'){
              this.myContent.scrollToPoint(0, this.targetx.nativeElement.offsetTop - 10, 100);
            }
          },1000);
        }else{
          this.errorMessage = result.message;
          this.brandDetailData="";
        }     
      }, err=>{
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
    });
  }









}